<template>
  <div>
    <h1>اطلاعات کاربری</h1>
    <div>
      <v-row class="mt-6">
        <v-col cols="12" sm="12" md="4">
          <v-text-field
            label="نام"
            v-model="profile.name"
            solo
            :loading="loading"
            hide-details=""
          ></v-text-field>
        </v-col>
        <v-col cols="12" sm="12" md="4">
          <v-text-field
            label="نام خانوادگی"
            v-model="profile.lastName"
            solo
            :loading="loading"
            hide-details=""
          ></v-text-field>
        </v-col>
        <v-col cols="12" sm="12" md="4">
          <v-text-field
            label="ایمیل"
            v-model="profile.email"
            solo
            :loading="loading"
            hide-details=""
          ></v-text-field>
        </v-col>
        <v-col cols="12" sm="12" md="6">
          <v-text-field
            label="آدرس"
            v-model="profile.address"
            solo
            :loading="loading"
            hide-details=""
          ></v-text-field>
        </v-col>
        <v-col cols="12" sm="12" md="12">
          <v-btn
            color="primary"
            width="150"
            @click="postProfile()"
            :loading="loading"
            >ویرایش</v-btn
          >
        </v-col>
      </v-row>
    </div>
  </div>
</template>
 
 <script>
import axios from "axios";
export default {
  name: "Profile",
  data() {
    return {
      loading: false,
      isFirstTime: true,
      profile: {
        email: "",
        address: "",
        name: "",
        lastName: "",
      },
      oldData: null,
    };
  },
  mounted() {
    this.getProfile();
  },
  methods: {
    async getProfile() {
      this.loading = true;
      try {
        const { data } = await axios({
          method: "GET",
          url: "/profile",
          headers: {
            Authorization: "Bearer " + this.$cookies.get("user-token"),
          },
        });
        this.profile = data;
        this.oldData = JSON.parse(JSON.stringify(data));
        this.isFirstTime = false;
      } catch {
        if (!this.isFirstTime) {
          this.$toast.error("خطایی رخ داد");
        } else this.$toast.warning("اطلاعات حساب خود را تکمیل کنید");
      } finally {
        this.loading = false;
      }
    },
    async postProfile() {
      this.loading = true;

      const dataToSend = {
        ...this.profile,
      };

      if (dataToSend.lastName.length < 3 || dataToSend.name.length < 3) {
        this.$toast.error("نام  و نام خانوادگی باید بیشتر از 3 کاراکتر باشد");
        return;
      }

      const reg = new RegExp(
        /^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/
      );
      if (!reg.test("" + dataToSend.email)) {
        this.$toast.error("ایمیل معتبر نمی باشد");
        return;
      }

      if (dataToSend.address.length < 8) {
        this.$toast.error("  آدرس باید بیشتر از 8 کاراکتر باشد");
        return;
      }

      if (dataToSend.email === this.oldData.email) delete dataToSend.email;
      if (dataToSend.name === this.oldData.name) delete dataToSend.name;
      if (dataToSend.lastName === this.oldData.lastName) delete dataToSend.lastName;
      if (dataToSend.address === this.oldData.address) delete dataToSend.address;

      // if(!this.isFirstTime)
      //   delete dataToSend.email
      try {
        await axios({
          method: this.isFirstTime ? "POST" : "PATCH",
          url: "/profile",
          headers: {
            Authorization: "Bearer " + this.$cookies.get("user-token"),
          },
          data: dataToSend,
        });

        this.getProfile()

        if (this.isFirstTime) {
          this.isFirstTime = false;
        }
      } catch {
        this.$toast.error("خطایی رخ داد");
      } finally {
        this.loading = false;
      }
    },
    async putProfile() {
      this.loading = true;
      try {
        await axios({
          method: "PATCH",
          url: "/profile",
          headers: {
            Authorization: "Bearer " + this.$cookies.get("user-token"),
          },
          data: this.profile,
        });
      } catch {
        this.$toast.error("خطایی رخ داد");
      } finally {
        this.loading = false;
      }
    },
  },
};
</script>
 
